(function() {
    angular.module('app')
        .factory('DrawingService', DrawingService);
    
    DrawingService.$inject = ['ShapeService'];
    function DrawingService(ShapeService) {
        
        var drawingFactory = {};
        
        drawingFactory.createShape = function(options) {
            var parentClass = null;
            console.log(options.shapeType);
            if(options.shapeType === 'rect') {
                parentClass = ShapeService.createRect;
            } else if(options.shapeType === 'ellipse') {
                parentClass = ShapeService.createEllipse;
            } else if(options.shapeType === 'triangle') {
                parentClass = ShapeService.createTriangle;
            }
            
            return new parentClass(options);
        }
        return drawingFactory;
    };
})();
(function() {
    angular.module('app')
            .factory('ShapeService', ShapeService);
    
    function ShapeService() {
        
        var shapeFactory = {};
        
        shapeFactory.createRect = function(options) {
            var rect = new fabric.Rect({
                left: options.startPosition.x,
                top: options.startPosition.y,
                width:10,
                height:10,
                stroke:'black',
                strokeWidth:1,
                fill:'transparent'
            });
            return rect;
        }
        shapeFactory.createEllipse = function(options) {
            var ellipse = new fabric.Ellipse({
                left: options.startPosition.x,
                top: options.startPosition.y,
                originX: 'left',
                originY: 'top',
                rx: 0,
                ry: 0,
                fill: 'transparent',
                stroke: 'black',
                strokeWidth: 1                
            });
            return ellipse;
        }
        shapeFactory.createTriangle = function(options) {
            var triangle = new fabric.Triangle({
                left: options.startPosition.x,
                top: options.startPosition.y,
                width:0,
                height:0,
                fill: 'transparent',
                stroke: 'black',
                strokeWidth: 1            
            });
            return triangle;
        }
        
        return shapeFactory;
    };
    
    
})();
(function () {
    angular
        .module('app')
        .service('authentication', authentication);
    
    authentication.$inject = ['$http', '$window'];
    function authentication($http, $window) {
        var saveToken = function(token) {
            $window.localStorage['mean-token'] = token;
        };
        
        var getToken = function() {
            return $window.localStorage['mean-token'];
        };
        
        logout = function() {
            $window.localStorage.removeItem('mean-token');
        };
        
        var isLoggedIn = function() {
            var token = getToken();
            var payload;
            
            if(token) {
                payload = token.split('.')[1];
                payload = $window.atob(payload);
                payload = JSON.parse(payload);
                
                return payload.exp > Date.now() / 1000;
            } else {
                return false;
            }
        };
        var currentUser = function() {
            if(isLoggedIn()) {
                var token = getToken();
                var payload = token.split('.')[1];
                payload = $window.atob(payload);
                payload = JSON.parse(payload);
                return {
                    email: payload.email,
                    name: payload.name
                };
            }
        };
        register = function(user) {
            return $http.post('/api/register', user).success(function(data) {
                saveToken(data.token);
            });
        };
        login = function(user) {
            return $http.post('/api/login', user).success(function(data) {
                saveToken(data.token);
            });
        };
        return {
            saveToken: saveToken,
            getToken: getToken,
            logout: logout,
            isLoggedIn: isLoggedIn,
            currentUser: currentUser,
            register: register,
            login: login,
            logout: logout
        };
    }
})();
